.MainContainer {
  display: flex;
  justify-content: center;
  height: 90vh;
  margin: 0;
  align-items: center;
  font-family: Helvetica, sans-serif;
}

.Container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  height: 80vh;
  width: 57vw;
  color: white;
  .Header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2rem;
    text-align: center;
    font-size: 17px;

    h1 {
      font-weight: bold;
      font-family: sans-serif;
      text-align: center;
      font-size: 3rem;
      border-bottom: 1px solid gray;
      width: 23rem;
      border-radius: 20px;
      margin-bottom: 2rem;
    }
    span {
      color: purple;
    }
  }
}

.Info {
  border-radius: 20px;
  display: flex;
  width: 60vw;
  height: 28rem;
  justify-content: space-between;

  .EmailInfo {
    background-color: black;
    border-radius: 10px;
    padding: 3rem;
    width: 22vw;

    h2 {
      margin-top: 2rem;
    }
    p {
      margin-top: 1rem;
      margin-bottom: 2rem;
    }
    .MoreInfo {
      display: flex;
      align-items: center;
      margin-top: 20px;
    }
    .icon {
      border: none;
      background: purple;
      color: white;
      font-size: 2.5rem;
      padding: 11px;
      border-radius: 50px;
      margin-right: 15px;
    }
    span {
      color: white;
      font-size: 0.9rem;
      div {
        color: gray;
      }
    }
  }
}

.FormInfo {
  background-color: black;
  border-radius: 10px;
  width: 37vw;
  padding: 1.7rem;
  .PartOne {
    display: flex;
    justify-content: space-between;
    label {
      font-size: 15px;
    }
    input {
      width: 20rem;
      background: transparent;
      border: 1px #353535 solid;
      border-radius: 5px;
      height: 2rem;
      margin-top: 5px;
      &::placeholder {
        padding: 10px;
      }
    }
  }
  .PartTwo {
    margin-top: 20px;
    label {
      font-size: 15px;
    }
    input {
      background: transparent;
      border: 1px #353535 solid;
      border-radius: 5px;
      height: 2rem;
      width: 41rem;
      margin-top: 5px;
      &::placeholder {
        padding: 10px;
      }
    }
  }
  .PartThree {
    margin-top: 20px;
    label {
      font-size: 15px;
    }
    textarea {
      background: transparent;
      border: 1px #353535 solid;
      border-radius: 5px;
      height: 10rem;
      width: 41rem;
      margin-top: 5px;
      &::placeholder {
        padding: 10px;
      }
    }
  }
  .PartFour {
    margin-top: 10px;
    input {
      color: white;
      background-color: purple;
      height: 45px;
      line-height: 45px;
      padding: 0 30px;
      font-size: 13px;
      text-transform: uppercase;
      font-weight: 600;
      border: none;
      border-radius: 3px;
      cursor: pointer;
      &:hover {
        background-color: #222121;
        border: purple 1px solid;
      }
    }
  }
}
